.navbar {
    padding: 0.8rem 0;
    background-color: #fff;
    box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, .1);
    margin-bottom: 2.5em;
}

.navbar ul {
    list-style: none;
    overflow: hidden;
    background-color: #fff;
}

.boton__contact {
    border: 2px solid #7510F7;
    border-radius: 9999px;
}

.boton__contact:hover {
    background-color: #7510F7;
    border-color: #7510F7;
    color: #fff;
    transition: all .2s ease-in-out;
}

.navbar li a {
    font-family: 'Quicksand', sans-serif;
    color: black;
    font-weight: 500;
    display: block;
    padding: 10px 10px;
    text-decoration: none;
}

li a:hover,
.menu-btn:hover {
    color: #7510F7;
}

.navbar__brand {
    float: left;
    font-size: 2em;
    padding: 10px 20px;
    text-decoration: none;
}

.navbar__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2em 0 1.5em;
}

.navbar__container>img {
    width: 3.3rem;
    height: auto;
}

.menu {
    text-align: center;
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
}

.menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
}

.menu-icon .navicon {
    background: #333;
    display: block;
    height: 1px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
}

.navbar .menu-icon .navicon:before,
.navbar .menu-icon .navicon:after {
    background: #333;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
}

.navbar .menu-icon .navicon:before {
    top: 5px;
}

.navbar .menu-icon .navicon:after {
    top: -5px;
}


.navbar .menu-btn {
    display: none;
}

.navbar .menu-btn:checked~.menu {
    max-height: 240px;
}

.navbar .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

.navbar .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
}

.navbar .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
}

.navbar .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
.navbar .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}


@media (min-width: 48em) {
    .navbar li {
        float: left;
    }

    .navbar li a {
        padding: 20px 30px;
    }

    .navbar .menu {
        clear: none;
        float: right;
        max-height: none;
    }

    .navbar .menu-icon {
        display: none;
    }
}